<template>
    <div>
      <!-- 查询 -->
      <common-wrapper>
        <stock-management-search class="search"
          :warehouseList="warehouseList"
          :merchantsList="merchantsList"
          :modelBrand="modelBrand"
          :brandList="brandList"
          :allModelList="modelList"
          @getMerchants="getMerchantsList"
          @getBrandModel="getBrandModel"
          @search="queryList"
          @rest="rest" />
      </common-wrapper>
      <!-- <header-breadcrumb :breadData="$route.matched" /> -->
      <nav-button>
        <el-button-group>
           <el-button icon="fdddfont icon-add-new" @click="handleExport">导出库存明细</el-button>
           <el-tooltip class="item" effect="light" content="点击选择列表数据查看明细详情" placement="right">
                  <el-button icon="fdddfont icon-shouquan" :disabled="!isButtonIsDisabled" @click="handelDetail">查看明细详情</el-button>
            </el-tooltip>
        </el-button-group>
      </nav-button>

     <!-- 库存管理列表 -->
      <common-wrapper>
        <common-table :flex="310">
          <template slot="table">
            <el-table
              ref="userTable"
              height="100%"
              :border="true"
              stripe
              highlight-current-row
              v-loading="isListLoading"
              :data="tableData"
              tooltip-effect="light"
              @row-click="getUserDetail"
              @current-change="selected => selectRow = selected">
                <el-table-column sortable prop="productMerchant" label="商户"></el-table-column>
                <el-table-column sortable prop="productBrand" label="品牌"></el-table-column>
                <el-table-column sortable prop="productName" label="货品名称" width="100" ></el-table-column>
                <el-table-column sortable prop="productItemName" label="子件名" width="100" :show-tooltip-when-overflow="true"></el-table-column>
              <el-table-column sortable prop="productModel" label="型号" :show-tooltip-when-overflow="true"></el-table-column>
                <el-table-column prop="productColor" label="颜色" :show-tooltip-when-overflow="true"></el-table-column>
                <el-table-column prop="productStandard" label="规格" :show-tooltip-when-overflow="true"></el-table-column>
               <el-table-column prop="productType" label="货品类型" :formatter="productTypeFormatter"></el-table-column>
              <el-table-column sortable prop="productCount" label="包装数"></el-table-column>
              <el-table-column sortable prop="goodCount" label="剩余数量"></el-table-column>
              <!--<el-table-column prop="goodCount" label="子件数"></el-table-column>-->
              <!--<el-table-column prop="productItems" label="子件名"></el-table-column>-->
                <!-- <el-table-column label="操作" width="190" >
                  <template slot-scope="scope">
                    <el-button size="mini" round icon="el-icon-view" @click.stop="handelDetail(scope.$index, scope.row)"></el-button>
                  </template>
                </el-table-column> -->
            </el-table>
          </template>
          <!-- <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template> -->
        </common-table>
      </common-wrapper>

     <!-- 新增、修改用户, 因二者格式不同,故分开两个组件调用 -->
     <stock-detail
        :show.sync="isDetailVisible"
        :searchData.sync="modifyStock"
      />
    </div>
</template>

<script>
/**
 * Stock Management  Module
 * ----------------------
 * Author: Calamus
 * Date: 2018.09.05
 */
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import StockManagementSearch from './Components/Search'
import StockDetail from './Components/stockDetail'
// API
import { setSession, getSession } from '@/utils/session'
import { getAllMerchantsList,getBrandModel } from '@/api/common'
import { getWarehouseList,stockManagementList, storageExport } from '@/api/warehouse'

export default {
  name: 'StockManagement',
  mixins: [mixins],
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: null, // 列表选中列
      isListLoading: false, // 列表加载
      warehouseList:this.$store.getters.warehouseList,
      merchantsList:[], //公用商户列表
      brandList:[], //商户列表
      modelList: [],
      modelBrand: [],
      isCreateFormVisible: false, // 新增界面是否显示
      isDetailVisible: false, // 编辑界面是否显示
      isRoleAuthVisible: false, // 用户角色授权窗口是否展示
      modifyStock: null, // 弹出框编辑时存放的临时行数据
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {} // 查询参数
    }
  },
  mounted () {

    // getAllBrandList().then(res => {
    //   this.brandList = res
    // })

    this.getMerchantsList()
  },
  computed:{
    isButtonIsDisabled(){
        return  this.selectRow !== null ;
    }
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params;
      this.getStockManagement(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 获取用户列表
    getStockManagement (params) {
      this.isListLoading = true
      stockManagementList(params).then(res => {
        this.tableData = res;
        this.$refs.userTable.setCurrentRow(this.tableData[0]);
        this.pagination.total = res.total
        this.isListLoading = false
      })
    },
    // 商户列表
      getMerchantsList (params={warehouseUuid: this.searchParams.warehouseUuid}) {
        return new Promise(resolve => {
          getAllMerchantsList(params).then(res => {
            if(!res.error) {
              this.merchantsList = res
              resolve()
            }
          })
        })
      },
    // 获取品牌型号列表
    getBrandModel (params) {
      getBrandModel(params).then(res => {
        if (!res.error) {
          this.brandList = []
          this.modelList = []
          res.forEach((item,index) => {
            this.brandList.push(item.productBrand)
            item.productModel.forEach(item2 => {
              this.modelList.push(item2)
            })
          })
          this.modelBrand = res
        }
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getStockManagement(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 显示详情界面
    handelDetail () {
      let searchParams = this.searchParams ;
      let selectRow = this.selectRow ;
      this.modifyStock =
      {
        ...searchParams,
        ...selectRow
        } ;
      this.isDetailVisible = true
    },
    isButtonDisabled () {
      return this.selectRow.id
    },
    // 查询详情
    getUserDetail (row, event) {
      // console.log(row)
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.queryList(this.searchParams)
    },
    // 列表日期格式化
    formatDate (row) {
      // return moment(row.userCreateTime).utcOffset(8).format('YYYY-MM-DD')
    },
    productTypeFormatter(row){
      return row.productType === '1' ? "套件" : row.productType === '0' ? "小件" : row.productType === '2' ? "单品" : "未知"
    },
    handleExport() {
      let params = this.searchParams;
      storageExport(params).then((res) => { // 处理返回的文件流
        const content = res.data
        const blob = new Blob([content], {type: 'application/ms-excel'})
        const fileName = '库存明细统计' + moment(new Date()).format('YYYYMMDDHHmm') + '.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      })
    }
  },
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    StockManagementSearch,
    StockDetail
  }
}
</script>



