<template>
    <el-dialog
    width="90%"
    title="库存详情"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="cancel"
    :lock-scroll="true"
    >
            <common-wrapper>
              <common-table :flex="350">
                <template slot="table">
                  <el-table
                    ref="DetailTable"
                    height="100%"
                    stripe
                    :border="true"
                    tooltip-effect="light"
                    highlight-current-row
                    v-loading="isListLoading"
                    :data="tableData">
                      <el-table-column prop="productMerchant" label="商户"  min-width="120"></el-table-column>
                      <el-table-column prop="productBrand" label="品牌"></el-table-column>
                      <el-table-column prop="name" label="货品名称" min-width="120" ></el-table-column>
                      <el-table-column prop="productItemName" label="子件名" :show-overflow-tooltip="true" min-width="120"></el-table-column>
                      <el-table-column prop="productModel" label="型号" min-width="120"></el-table-column>
                      <el-table-column prop="productColor" label="颜色"></el-table-column>
                      <!--<el-table-column prop="productMain" label="货品类型"></el-table-column>-->
                      <!--<el-table-column prop="productCount" label="包装数"></el-table-column>-->
                      <!--<el-table-column prop="productItemNum" label="子件数"></el-table-column>-->
                    <el-table-column prop="productStandard" label="规格"></el-table-column>
                    <el-table-column prop="storageName" label="库位"></el-table-column>
                     <el-table-column prop="productRemarks" label="备注" min-width="120" :show-tooltip-when-overflow="true"></el-table-column>
                      <el-table-column prop="receiveDate" label="入库时间" :formatter="formatDate"  width="150"></el-table-column>
                      <el-table-column prop="productRetainTime" label="货物存货时间" width="120"></el-table-column>
                  </el-table>
                </template>
                <!-- <template slot="pagination">
                  <el-pagination   background   layout="total, prev, pager, next"
                      :current-page.sync="pagination.pageNum"
                      :page-size="pagination.pageSize"
                      :total="pagination.total"
                      @current-change="changePage"
                      >
                  </el-pagination>
                </template> -->
              </common-table>
            </common-wrapper>

    </el-dialog>
</template>
<script>
/**
 * Stock Management Detail Component
 * ----------------------
 * Author: Calamus
 * Date: 2018.09.06
 */

import moment from 'moment'

// api
import { stockManagementDetail, stockManagementList } from '@/api/warehouse'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'

export default {
  name: 'StockDetail',
  components: {
    CommonWrapper,
    CommonTable
  },
  data () {
    return {
      isVisible: false,
      id: '',
      tableData: [],
      isListLoading: false,
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      }
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    modifyUser: {
      type: Object
    },
    searchData: {
      type: Object
    }
  },
  watch: {
    show (isShow) {
      this.isVisible = isShow
    },
    modifyUser (user) {
      // 显示对话框后,如果为编辑状态(有用户数据传入),则合并表单数据
      // console.log('user', user)
      if (user) {
        const formData = Object.create(null)
        for (let [key, value] of Object.entries(user)) {
          if (this.userForm[key] !== undefined) formData[key] = value
        }
        this.userForm.userUuid = user.userUuid
        this.userForm = Object.assign({}, this.userForm, formData)
      }
    },
    searchData (res) {
      console.log('res', res)
      this.isListLoading = true
      // const params = Object.create(null),
      if (res) {
        this.getDetail(res)
      } else {
        console.log('resnull')
      }
    }
  },
  methods: {
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
      this.getDetail(this.searchData)
    },
    cancel () {
      this.isVisible = false
      this.$emit('update:show', false)
      this.$emit('update:searchData', null)
    },
    getDetail (params) {
      stockManagementDetail(params).then(res => {
        this.tableData = res
        // this.pagination.total = res.total
        this.isListLoading = false
      })
    },
    // 列表日期格式化
    formatDate (row) {
      if (row.receiveDate) { return moment(row.receiveDate).utcOffset(8).format('YYYY-MM-DD HH:mm:ss') }
    },
    // 分钟格式化
    formatMinute (row) {
      if (row.productRetainTime) {
        let minute = Number(row.productRetainTime.split('分')[0])
        return ((Math.floor(minute / 60)).toString().length < 2 ? '0' + (Math.floor(minute / 60)).toString()
          : (Math.floor(minute / 60)).toString()) + ':' + ((minute % 60).toString().length < 2 ? '0' + (minute % 60).toString() : (minute % 60).toString())
      }
    }
  }
}
</script>



