<template>
     <el-form :model="searchForm" ref="searchForm"  label-width="80px" >
            <el-row>
                <el-col :span="6">
                    <el-form-item label="仓库" prop="warehouseUuid">
                      <el-select placeholder="请选择仓库" v-model="searchForm.warehouseUuid"  @change="getMerchantsList">
                          <el-option
                            v-for="(item, index) in warehouseList"
                            :key="index"
                            :label="item.warehouseName"
                            :value="item.warehouseUuid"
                          >{{item.warehouseName}}</el-option>
                      </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="商户" prop="merchantUuid">
                      <el-select clearable placeholder="请选择商户"  filterable @change="merchantChange" v-model="searchForm.merchantUuid" >
                          <el-option
                            v-for="(item, index) in merchantsList"
                            :key="index"
                            :label="item.userName"
                            :value="item.userUuid"
                          >{{item.userName}}</el-option>
                      </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="品牌" prop="productBrand">
                      <el-select  placeholder="请选择品牌" clearable filterable @change="brandChange"  v-model="searchForm.productBrand" >
                          <el-option
                            v-for="(item, index) in brandList"
                            :key="index"
                            :label="item"
                            :value="item"
                          >{{item}}</el-option>
                      </el-select>
                  </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6">
                    <el-form-item label="开始日期"  prop="beginDate">
                      <el-date-picker
                        class="w100p"
                        v-model="searchForm.beginDate"
                        type="date"
                        align="right"
                        clearable
                        unlink-panels
                        range-separator="至"
                        placeholder="请选择开始日期"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd"
                        size="mini"
                        >
                      </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="结束日期"  prop="endDate">
                      <el-date-picker
                        class="w100p"
                        v-model="searchForm.endDate"
                        type="date"
                        align="right"
                        clearable
                        unlink-panels
                        range-separator="至"
                        placeholder="请选择结束日期"
                        format="yyyy 年 MM 月 dd 日"
                        value-format="yyyy-MM-dd"
                        size="mini"
                        >
                      </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="型号"  prop="productModelName">
                      <el-select  placeholder="请选择型号" clearable filterable v-model="searchForm.productModelName" >
                        <el-option
                          v-for="(item, index) in modelList"
                          :key="index"
                          :label="item"
                          :value="item"
                        >{{item}}</el-option>
                      </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6">
                  <el-form-item label="货品类型" prop="productType">
                    <el-select v-model="searchForm.productType" clearable filterable placeholder="请选择货品类型" >
                      <el-option label="小件" value="0"/>
                      <el-option label="套件" value="1"/>
                      <el-option label="单品" value="2"/>
                    </el-select>
                  </el-form-item>
                </el-col>

                <!-- <el-col :span="6">
                    <el-form-item label="备注"  prop="productRemark">
                      <el-input v-model="searchForm.productRemark"  clearable></el-input>
                    </el-form-item>
                </el-col> -->
                <el-col :span="6" class="flex-right">
                    <el-button type="primary" @click.native="search">查询</el-button>
                    <el-button @click="resetForm('searchForm')">重置</el-button>
                </el-col>
            </el-row>
          </el-form>
</template>

<script>
/**
 * StockManagement Search Component
 * ----------------------
 * Author: Calamus
 * Date: 2018.09.05
 */
import { setSession, getSession } from '@/utils/session'
import moment from 'moment'


export default {
  name: 'StockManagementSearch',
  data () {
    return {
      modelList: [],
      // 检索界面数据
      searchForm: {
        warehouseUuid: '',
        merchantUuid: '',
        productBrand: '',
        entryTime: [],
        beginDate: '',
        endDate: '',
        productModelName: '',
        productType: '',
        productRemark: ''
      }
    }
  },
  props: {
    warehouseList: {
      type: Array,
      required: true
    },
    merchantsList: {
      type: Array,
      required: true
    },
    brandList: {
      type: Array
    },
    allModelList: {
      type: Array
    },
    modelBrand: {
      type: Array
    }
  },
  watch: {
    allModelList (val) {
      this.modelList = Object.assign({}, val)
    }
  },
  activated () {
    this.searchForm = {
      warehouseUuid: '',
      merchantUuid: '',
      productBrand: '',
      entryTime: [],
      beginDate: '',
      endDate: '',
      productModelName: '',
      productType: '',
      productRemark: ''
    }
    this.searchForm.warehouseUuid = getSession('warehouseUuid')
    this.$emit('getBrandModel', {
      warehouseUuid: getSession('warehouseUuid')
    })
    this.search()
  },
  mounted () {
    this.searchForm.warehouseUuid = getSession('warehouseUuid')
    this.search()
  },
  methods: {
    // 检索
    search: function () {
      if (this.searchForm.endDate && this.searchForm.beginDate && ( moment(this.searchForm.endDate).isBefore(this.searchForm.beginDate) || moment(this.searchForm.endDate).isSame(this.searchForm.beginDate))) {
              this.$message({
                message: '结束日期不能早于开始日期，请重新选择！',
                type: 'warning'
              })
              this.searchForm.endDate = null ;
              return
      }else{
        this.$emit('search', Object.assign({}, this.searchForm))
      }
    },
    getMerchantsList (value) {
      setSession('warehouseUuid', value)
      this.searchForm.merchantUuid = ''
      this.$emit('getMerchants', Object.assign({}, this.searchForm))
      this.$emit('getBrandModel', {
        warehouseUuid: getSession('warehouseUuid')
      })
    },
    merchantChange (value) {
      this.searchForm.productBrand = ''
      this.searchForm.productModelName = ''
      this.$emit('getBrandModel', {
        warehouseUuid: getSession('warehouseUuid'),
        merchantUuid: this.searchForm.merchantUuid
      })
    },
    // 获取型号
    brandChange (value) {
      this.searchForm.productModelName = ''
      if (value === '') {
        this.modelList = Object.assign({}, this.allModelList)
      } else {
        let model = []
        model = this.modelBrand.find(item => {
          return item.productBrand === value
        })
        this.modelList = model.productModel
      }
    },
    // 关闭查询框
    closeSearch: function (formName) {
      this.$emit('closeSearch')
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.searchForm.warehouseUuid = getSession('warehouseUuid')
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }
}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



